import React from 'react'
import Head from 'next/head'
import fetch from 'isomorphic-unfetch'

export default class extends React.Component {
  static async getInitialProps() {
    const res = await fetch('http://jsonstub.com/meta', {
      type: 'GET',
      contentType: 'application/json',
      beforeSend: function (request) {
        request.setRequestHeader('JsonStub-User-Key', 'f4dbf5e1-0ff1-4f72-b0fd-44170432a544');
        request.setRequestHeader('JsonStub-Project-Key', '7aceacc3-139f-4cc7-9903-a54f43d7b9b9');
      }
    })
    const data = await res.json()

    console.log(`data: ${data}`)

    return {
      data: data
    }
  }

  render() {
    console.log(this.props.data)
    return (
      <div>
        <Head>
          <title>My page title</title>
          <meta name="description" content="Free Web tutorials"/>
          <meta name="keywords" content="HTML,CSS,XML,JavaScript"/>
        </Head>
        <p>Hello world!</p>
      </div>
    )
  }
}



